<template>
  <v-app>
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-start justify-center>
          <v-flex xs12 sm8 md4>
            <v-toolbar dark color='teal lighten-2'>
              <v-toolbar-title>keyword</v-toolbar-title>
              <v-autocomplete
                :loading='loading'
                :items='items'
                :search-input.sync='search'
                v-model='select'
                cache-items
                class='mx-3'
                flat
                hide-no-data
                hide-details
                hide-selected
                solo-inverted
                @keyup.enter="submit(select)"
              ></v-autocomplete>
              <v-btn icon @click="submit(select)">
                <v-icon>search</v-icon>
              </v-btn>
            </v-toolbar>
            <component v-bind:is="component"></component>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import axios from 'axios'
import test from './Chart.vue'

export default {
  name: 'Search',
  components: {
    'chart': test
  },
  created () {
    axios.get('/api/today').then((response) => {
      this.states = response.data
    }).catch((error) => {
      console.log(error)
    })
  },
  data () {
    return {
      loading: false,
      items: [],
      search: null,
      select: null,
      states: [],
      component: ''
    }
  },
  watch: {
    search (value) {
      value && value !== this.select && this.simulatedQuery(value)
    }
  },
  methods: {
    // mysql
    submit (message, event) {
      axios.get('/api/emotion').then((response) => {
        this.component = 'chart'
      }).catch(() => {})
    },
    simulatedQuery (v) {
      this.loading = 'info'
      this.items = this.states.filter(e => {
        return (e || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
      })
      this.loading = false
    }
  }
}
</script>



